@import 'colors';

.home {
    width: 100%;
    height: 100vh;
    background-color: $second;
    border-radius: 0px 0px 200px 200px;

    >main {
        position: absolute;
        display: flex;
        text-transform: uppercase;
        flex-direction: column;
        align-items: flex-end;
        width: 100%;
        height: 100%;
        padding: 2rem;
        justify-content: center;

        h1 {
            font: 400 4rem "Roboto";
            color: $first;
        }

        p {
            font: 400 1rem "Roboto";
            color: $first;
        }
    }

    &::before {
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        background: no-repeat center/cover url('../assets/2.jpg');
        filter: blur(3px);
        border-radius: 0px 0px 200px 200px;
    }
}

.home2 {
    width: 100%;
    height: 160vh;
    background-color: $second;
    padding: 400px 0;
    display: flex;
    justify-content: center;
    text-align: center;
    margin-top: -200px;

    >img {
        width: 30%;
        animation: imgUp 0.7s linear infinite alternate;
    }

    >div {
        display: flex;
        width: 50%;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 5rem;

        h1 {
            color: $first;
            font: 600 3rem $text1;
        }

        >p {
            color: $first;
            letter-spacing: 2px;
            word-spacing: 5px;
            font: 400 1.2rem $text2;
        }
    }

}

@keyframes imgUp {
    to {
        transform: translateY(-10px);
    }
}

.home3 {
    width: 100%;
    height: 120vh;
    background-color: $second;
    display: flex;
    text-align: center;
    margin-top: -200px;

    >div {
        display: flex;
        width: 70%;
        height: 100vh;
        background-color: $first;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 2rem;
        border-radius: 0px 200px 200px 0px;

        h1 {
            color: $dark;
            font: 600 3rem $text1;
            text-transform: uppercase;
            border-bottom: 2px solid #000;
        }

        >p {
            color: $dark;
            letter-spacing: 2px;
            word-spacing: 5px;
            font: 400 1.2rem $text2;
            margin-top: 20px;
        }
    }

}

.home4 {
    width: 100%;
    height: 100vh;
    background-color: $second;
    display: flex;
    justify-content: flex-end;

    >div {
        background-color: $third;
        border-radius: 200px 0px 0px 200px;
        
        width: 70%;
        height: 70vh;
        padding: 2rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-align: center;

        >h1 {
            color: $first;
            text-decoration:underline ;
            padding: 0.5rem;
        }

        >article{
            display: flex;
            margin: 2rem 0;
            flex-wrap: wrap;
            justify-content: center;
            >div{
                animation: imgUp 0.7s linear infinite alternate;
                margin: 1rem;
                height: 10rem;
                width: 10rem;
                background-color: $first;
                border-radius: 50%;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                cursor: pointer;
                color: $dark;
                transition: all 0.5s;
                &:hover{
                    filter: invert(1);
                }
                svg{
                    font-size: 4rem;
                }
                p{
                    font:900 1.2rem $text1
                }
            }
        }

    }
}