@import 'colors';

.contact {
    width: 100%;
    height: 100vh;
    background-color: $second;
    display: flex;
    justify-content: center;
    text-align: center;
   

    >main {
        background-color: $first;
        height: 32rem;
        width: 70%;
        margin: 5rem;
        padding: 2rem;
        border-radius: 0 200px 200px 0;

        >form {
            margin: 5rem;
            display: flex;
            flex-direction: column;
            text-align: center;
            justify-content: center;
            >div{
                height: 3rem;
                display: flex;
                text-align: center;
                justify-content: flex-end;
                width: 80%;
                margin: 0.5rem;
                >label{
                  font:400 1.2rem $text1;
                  width:20%;
                  margin: 0 1rem;
                }
                >input,textarea{
                    align-self: stretch;
                    width: 80%;
                    padding: 1rem;
                    border-radius: 5px;
                    border:1px solid rgba(123,123,123,0.6);
                    outline: none;
                }
            }
            >button{
                margin: 3rem 0;
                padding: 0.7rem;
                border: none;
                border-radius: 5px;
                background-color: $third;
                color: $first;
                width: 200px;
                display: flex;
                text-align: center;
                justify-content: center;
                font:500 1rem $text1;
                &:hover{
                    opacity: 0.8;
                }
            }
        }
    }
}